/* GLOBAL */
@font-face {
  font-family: "Montserrat Regular";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Medium";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Semi Bold";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Bold";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Extra Bold";
  src: url("../fonts/montserrat-extrabold.woff2") format("woff2"), url("../fonts/montserrat-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  cursor: default;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

input {
  outline: none;
}

button,
a {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  max-width: 1440px;
}

/* BLOCKS */
.page-header {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .page-header__wrapper {
    display: flex;
    justify-content: flex-start;
    width: auto;
    gap: 30px;
  }
}

.page-header__logo-wrapper {
  padding: 12px 0 8px;
}
@media (min-width: 768px) {
  .page-header__logo-wrapper {
    padding: 22px 0 18px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-wrapper {
    display: flex;
  }
}

.page-header__logo {
  width: 32px;
  height: 36px;
  cursor: default;
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 170px;
    margin-right: 20px;
  }
}

.page-header__slogan {
  display: none;
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
}
@media (min-width: 1440px) {
  .page-header__slogan {
    display: block;
    width: 170px;
    margin: 0;
    padding-top: 3px;
  }
}

.main-nav {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .main-nav {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    flex-direction: row-reverse;
  }
}

.main-nav__list a {
  text-transform: uppercase;
}

.main-nav__toggle {
  display: block;
  width: 18px;
  height: 17px;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle {
  width: 18px;
  height: 21px;
  background: url("../img/svg/burger.svg") no-repeat 0 0;
}

.main-nav--opened .main-nav__toggle {
  width: 18px;
  height: 21px;
  background: url("../img/svg/close-icon.svg") no-repeat 0 0;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .site-list__item {
  display: block;
}

.main-nav--nojs .site-list__item--active a {
  padding-right: 21px;
  padding-left: 21px;
  text-align: left;
}

.main-nav--nojs .user-list__item {
  display: block;
  background-color: #6a4d38;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}

.site-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    padding-right: 20px;
  }
}

.site-list__item {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  width: 100%;
  padding: 20px;
  border-top: 1px solid #e0ebff;
  border-bottom: 2px solid transparent;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
}
.site-list__item:hover {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .site-list__item:hover {
    border-bottom: 2px solid #7859cf;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .site-list__item {
    padding: 32px 20px 30px;
    border-top: none;
  }
}

.promo {
  position: relative;
  background-color: #f3ebe1;
}
@media (min-width: 768px) {
  .promo {
    background: linear-gradient(180deg, #f3ebe1 77%, transparent 50%);
  }
}
@media (min-width: 1440px) {
  .promo {
    background-color: #f3ebe1;
    padding-bottom: 60px;
  }
}

.promo__arrows .swiper-button-next {
  position: absolute;
  top: 328px;
  right: 0;
  background-image: url("../../img/svg/arrow-right.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  color: transparent;
  border: none;
  background-color: transparent;
}
.promo__arrows .swiper-button-next:hover {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .promo__arrows .swiper-button-next {
    right: 30px;
    top: 320px;
  }
}
@media (min-width: 1440px) {
  .promo__arrows .swiper-button-next {
    right: 30px;
    top: 357px;
  }
}

.promo__arrows .swiper-button-prev {
  position: absolute;
  top: 328px;
  left: 0;
  background-image: url("../../img/svg/arrow-left.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  z-index: 1;
  color: transparent;
  border: none;
  background-color: transparent;
}
.promo__arrows .swiper-button-prev:hover {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .promo__arrows .swiper-button-prev {
    left: 30px;
    top: 320px;
  }
}
@media (min-width: 1440px) {
  .promo__arrows .swiper-button-prev {
    right: 30px;
    top: 357px;
  }
}

.slider {
  position: relative;
  padding: 20px 0 60px;
}
@media (min-width: 768px) {
  .slider {
    padding: 54px 0 0;
  }
}
@media (min-width: 1440px) {
  .slider {
    padding: 0;
  }
}

@media (min-width: 1440px) {
  .slider__list {
    padding-bottom: 20px;
  }
}

.slider__badge {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  padding-top: 6px;
}
@media (min-width: 1440px) {
  .slider__badge {
    padding: 0;
  }
}

.slider__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .slider__item {
    flex-direction: column-reverse;
  }
}
@media (min-width: 1440px) {
  .slider__item {
    display: grid;
    grid-template-columns: 600px 1fr;
  }
}

@media (min-width: 768px) {
  .slider__item img {
    width: 476px;
  }
}
@media (min-width: 1440px) {
  .slider__item img {
    width: 600px;
    height: 600px;
    padding: 29px 0 0 105px;
  }
}

.slider__heading {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 36px;
  line-height: 48px;
  color: #333333;
  text-align: center;
  margin: 0;
  padding-top: 5px;
}
@media (min-width: 768px) {
  .slider__heading {
    font-size: 56px;
    line-height: 80px;
  }
}
@media (min-width: 1440px) {
  .slider__heading {
    padding: 0;
    text-align: left;
    margin-bottom: 20px;
  }
}

.slider__desc-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .slider__desc-wrapper {
    padding-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .slider__desc-wrapper {
    width: 590px;
    align-items: flex-start;
    padding-bottom: 0;
    margin-left: 135px;
    margin-top: 20px;
  }
}

.slider__desc {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  text-align: center;
  margin: 0;
  padding: 10px 20px 19px;
}
@media (min-width: 768px) {
  .slider__desc {
    font-size: 20px;
    line-height: 32px;
    padding: 15px 85px 19px;
  }
}
@media (min-width: 1440px) {
  .slider__desc {
    padding: 0;
    text-align: left;
    margin-bottom: 40px;
  }
}

.slider__price-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.slider__old-price {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  color: #7859cf;
  text-decoration: line-through;
  padding-right: 5px;
}

.slider__price {
  font-family: "Montserrat Semi Bold", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  color: #333333;
}

.slider__btn {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  background-color: #7859cf;
  padding: 8px 20px;
  border: none;
  border-radius: 4px;
  transition: 0.3s;
}
.slider__btn:hover {
  opacity: 0.5;
}

.slider__buy {
  display: flex;
  justify-content: space-between;
  width: 230px;
}
@media (min-width: 1440px) {
  .slider__buy {
    padding: 0;
  }
}

@media (min-width: 1440px) {
  .slider__toggles {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}

.swiper-pagination-bullet {
  display: none;
  border-radius: 0;
}
.swiper-pagination-bullet:hover, .swiper-pagination-bullet:focus {
  color: rgba(120, 89, 207, 0.25);
}
.swiper-pagination-bullet:active {
  color: #7859cf;
}
@media (min-width: 1440px) {
  .swiper-pagination-bullet {
    display: block;
    content: "";
    width: 60px;
    height: 4px;
    background-color: #ffffff;
  }
}

.swiper-pagination-bullet-active {
  background-color: #7859cf;
  border-radius: 0;
}

.features {
  background-color: #ffffff;
}

.features__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0 27px;
}
@media (min-width: 768px) {
  .features__wrapper {
    padding: 56px 0 0 0;
  }
}
@media (min-width: 1440px) {
  .features__wrapper {
    padding: 101px 20px 0;
  }
}

.features__title {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 5px;
}

.features__subtitle {
  font-family: "Montserrat Semi Bold", "Arial", sans-serif;
  font-size: 24px;
  line-height: 35px;
  text-align: center;
  margin: 0;
  padding-bottom: 37px;
}
@media (min-width: 768px) {
  .features__subtitle {
    font-size: 42px;
    line-height: 64px;
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .features__subtitle {
    font-family: "Montserrat Bold", "Arial", sans-serif;
    font-size: 42px;
    line-height: 64px;
  }
}

.features__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 27px;
    grid-row-gap: 45px;
    padding: 55px 27px 101px;
  }
}
@media (min-width: 1440px) {
  .features__list {
    grid-template-columns: repeat(4, 1fr);
    padding: 77px 0 125px;
  }
}

.features__item {
  text-align: center;
  padding-bottom: 32px;
}
@media (min-width: 768px) {
  .features__item {
    display: flex;
    text-align: left;
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .features__item {
    align-items: center;
    text-align: center;
    flex-direction: column;
  }
}

.features__heading {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 32px;
  margin: 0;
  padding: 5px 0 5px;
}
@media (min-width: 768px) {
  .features__heading {
    font-size: 24px;
    line-height: 36px;
    padding: 5px 0 10px;
  }
}
@media (min-width: 1440px) {
  .features__heading {
    padding: 0;
    padding-bottom: 10px;
  }
}

.features__desc {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}
@media (min-width: 768px) {
  .features__desc {
    font-size: 16px;
    line-height: 24px;
  }
}

@media (min-width: 1440px) {
  .features__desc span {
    display: inline-block;
  }
}

.features__speed-icon,
.features__fit-icon,
.features__energy-icon,
.features__eco-icon {
  width: 52px;
  height: 56px;
}
@media (min-width: 768px) {
  .features__speed-icon,
.features__fit-icon,
.features__energy-icon,
.features__eco-icon {
    width: 103px;
  }
}

@media (min-width: 768px) {
  .features__item-wrapper {
    padding-left: 21px;
  }
}
@media (min-width: 1440px) {
  .features__item-wrapper {
    padding-left: 0;
  }
}

@media (min-width: 768px) {
  .features__item-img {
    padding: 8px 0 0 2px;
  }
}
@media (min-width: 1440px) {
  .features__item-img {
    padding-top: 0;
    padding-bottom: 20px;
  }
}

.catalog {
  background-color: #f8f8f8;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .catalog {
    padding-top: 78px;
  }
}
@media (min-width: 1440px) {
  .catalog {
    padding-top: 95px;
  }
}

@media (min-width: 768px) {
  .catalog__list {
    padding-top: 40px;
  }
}
@media (min-width: 1440px) {
  .catalog__list {
    padding-top: 0;
  }
}

@media (min-width: 1440px) {
  .catalog__wrap {
    display: grid;
    grid-template-columns: 340px 1fr;
    gap: 30px;
  }
}

.catalog__title {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 5px;
  text-align: center;
}
@media (min-width: 768px) {
  .catalog__title {
    padding-bottom: 9px;
  }
}

.catalog__subtitle {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  margin: 0;
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .catalog__subtitle {
    font-size: 42px;
    line-height: 54px;
    padding-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .catalog__subtitle {
    padding-bottom: 42px;
  }
}

@media (min-width: 768px) {
  .filter form {
    display: grid;
    grid-template-areas: "price fieldsets" "buttons buttons";
    grid-template-columns: 230px 1fr;
    column-gap: 20px;
  }
}
@media (min-width: 1440px) {
  .filter form {
    display: flex;
    flex-direction: column;
  }
}

@media (min-width: 1440px) {
  .filter {
    margin-left: 20px;
  }
}

@media (min-width: 768px) {
  .filter__fieldsets {
    grid-area: fieldsets;
    display: flex;
    gap: 40px;
  }
}
@media (min-width: 1440px) {
  .filter__fieldsets {
    flex-direction: column;
    gap: 0;
  }
}

.filter__price {
  grid-area: price;
  margin-bottom: 20px;
  width: 280px;
}
@media (min-width: 768px) {
  .filter__price {
    width: 230px;
  }
}
@media (min-width: 1440px) {
  .filter__price {
    width: 320px;
    padding-bottom: 6px;
  }
}

.filter__price-controls span {
  display: flex;
  align-items: center;
}

.filter__buttons-wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .filter__buttons-wrapper {
    flex-direction: row;
    gap: 20px;
  }
}
@media (min-width: 1440px) {
  .filter__buttons-wrapper {
    flex-direction: row;
    margin-left: 3px;
  }
}

.filter__heading {
  font-family: "Montserrat Semi Bold", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 34px;
  margin: 0;
  padding: 0;
}

.filter__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
}
@media (min-width: 768px) {
  .filter__buttons {
    grid-area: buttons;
    margin-top: 20px;
  }
}

.filter__btn {
  width: 160px;
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  padding: 10px 9px;
  border: none;
  border-radius: 4px;
  transition: 0.3s;
}
.filter__btn--apply {
  color: #ffffff;
  background-color: #7859cf;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .filter__btn--apply {
    margin-bottom: 0;
  }
}
.filter__btn--reset {
  color: #7859cf;
  background-color: #ffffff;
}
.filter__btn:hover {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .filter__btn {
    padding: 8px 4px;
  }
}
@media (min-width: 1440px) {
  .filter__btn {
    width: 150px;
  }
}

.filter__form fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.filter__price-controls {
  display: flex;
  justify-content: space-between;
  padding-top: 12px;
}
@media (min-width: 768px) {
  .filter__price-controls {
    padding-top: 8px;
  }
}
@media (min-width: 1440px) {
  .filter__price-controls {
    padding-left: 0;
  }
}

.filter__price-controls-input {
  width: 103px;
  color: #333333;
  border: 2px solid #9070ec;
  border-radius: 4px;
  padding: 10px 6px;
  text-align: center;
  -moz-appearance: textfield;
}
.filter__price-controls-input::-webkit-outer-spin-button, .filter__price-controls-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
@media (min-width: 768px) {
  .filter__price-controls-input {
    width: 77px;
  }
}
@media (min-width: 1440px) {
  .filter__price-controls-input {
    width: 120px;
  }
}

@media (min-width: 768px) {
  .filter__price-controls-input--min {
    margin-left: 8px;
  }
}
@media (min-width: 1440px) {
  .filter__price-controls-input--min {
    margin-left: 0;
  }
}

.filter__price-controls-input::placeholder {
  color: #bdbdbd;
  text-align: center;
}

.filter__range-controls {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  padding: 24px 0;
}
.filter__range-controls:disabled {
  color: #bdbdbd;
}

.filter__range-scale {
  height: 5px;
  background: #e2e2e2;
}

.filter__range-bar {
  height: 5px;
