.stock-list__radio {
  position: absolute;
  width: 24px;
  height: 24px;
  margin-left: -2em;
  background: url("../img/svg/radio/unchecked.svg") no-repeat 0 0;
  transition: 0.3s;
}
.stock-list__radio:hover {
  opacity: 0.5;
}

/* Checked */
.stock-list__input:checked + .stock-list__radio {
  background: url("../img/svg/radio/checked.svg") no-repeat 0 0;
}

/* Focused */
.stock-list__input:not(:checked):hover + .stock-list__radio {
  background: url("../img/svg/radio/hover-focus.svg") no-repeat 0 0;
}

/* Disabled */
.stock-list__input:disabled + .stock-list__radio {
  background: url("../img/svg/radio/disabled.svg") no-repeat 0 0;
}

.stock-list__input:checked:disabled + .stock-list__radio {
  background: url("../img/svg/radio/disabled-checked.svg") no-repeat 0 0;
}

